.search-m-panel-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  transition: all 0.25s ease-in-out;
}

.search-m-panel {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  opacity: 0;
  visibility: hidden;
  background-color: #fff;
  transition: all 0.25s ease-in-out;
}

.search-m-panel.open {
  opacity: 1;
  visibility: visible;
  z-index: 999;
}

.search-m-panel .search-result {
  box-shadow: none;
  margin-top: 10px;
  right: 0;
}

.search-m-panel__cont {
  height: 100%;
  padding-top: 60px;
}

.search-m-panel__inner {
  height: 100%;
  overflow: auto;
  padding: 0 15px;
}